/* AgeCalculator.css */
.age-calculator-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: #f7f7f7;
    border-radius: 10px;
}

input[type="date"] {
    width: 90%;
    padding: 10px;
    font-size: 18px;
    margin-bottom: 10px;
}

button {
    width: 100%;
    padding: 10px;
    font-size: 18px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 10px;
}

button:hover {
    background-color: #35577b;
}

p {
    font-size: 24px;
    margin-top: 10px;
}